const actions = {
  GET_JOB_TITLES_REQUEST: "GET_JOB_TITLES_REQUEST",
  GET_JOB_TITLES_SUCCESS: "GET_JOB_TITLES_SUCCESS",
  STORE_JOB_TITLE_REQUEST: "STORE_JOB_TITLE_REQUEST",
  STORE_JOB_TITLE_SUCCESS: "STORE_JOB_TITLE_SUCCESS",
  STORE_JOB_TITLE_ERROR: "STORE_JOB_TITLE_ERROR",
  EDIT_JOB_TITLE_REQUEST: "EDIT_JOB_TITLE_REQUEST",
  EDIT_JOB_TITLE_SUCCESS: "EDIT_JOB_TITLE_SUCCESS",
  EDIT_JOB_TITLE_ERROR: "EDIT_JOB_TITLE_ERROR",
  UPDATE_JOB_TITLE_REQUEST: "UPDATE_JOB_TITLE_REQUEST",
  UPDATE_JOB_TITLE_SUCCESS: "UPDATE_JOB_TITLE_SUCCESS",
  UPDATE_JOB_TITLE_ERROR: "UPDATE_JOB_TITLE_ERROR",
  SHOW_JOB_TITLE_REQUEST: "SHOW_JOB_TITLE_REQUEST",
  SHOW_JOB_TITLE_SUCCESS: "SHOW_JOB_TITLE_SUCCESS",
  SHOW_JOB_TITLE_ERROR: "SHOW_JOB_TITLE_ERROR",
  DEACTIVATE_JOB_TITLE_REQUEST: "DEACTIVATE_JOB_TITLE_REQUEST",
  DEACTIVATE_JOB_TITLE_SUCCESS: "DEACTIVATE_JOB_TITLE_SUCCESS",
  DEACTIVATE_JOB_TITLE_ERROR: "DEACTIVATE_JOB_TITLE_ERROR",
  ACTIVATE_JOB_TITLE_REQUEST: "ACTIVATE_JOB_TITLE_REQUEST",
  ACTIVATE_JOB_TITLE_SUCCESS: "ACTIVATE_JOB_TITLE_SUCCESS",
  ACTIVATE_JOB_TITLE_ERROR: "ACTIVATE_JOB_TITLE_ERROR",
  CLEAR_SUCCESS: "CLEAR_SUCCESS",
  CLEAR_ERROR: "CLEAR_ERROR",
  CLEAR_ERROR_FIELD: "CLEAR_ERROR_FIELD",
  getJobTitles: (page, search) => ({
    type: "GET_JOB_TITLES_REQUEST",
    payload: {
      page: page,
      search: search
    }
  }),
  storeJobTitle: data => ({
    type: "STORE_JOB_TITLE_REQUEST",
    payload: { body: data }
  }),
  editJobTitle: id_job_title => ({
    type: "EDIT_JOB_TITLE_REQUEST",
    payload: { id_job_title: id_job_title }
  }),
  updateJobTitle: (id_job_title, data) => ({
    type: "UPDATE_JOB_TITLE_REQUEST",
    payload: {
      id_job_title: id_job_title,
      body: data
    }
  }),
  showJobTitle: id_job_title => ({
    type: "SHOW_JOB_TITLE_REQUEST",
    payload: { id_job_title: id_job_title }
  }),
  deactivateJobTitle: id_job_title => ({
    type: "DEACTIVATE_JOB_TITLE_REQUEST",
    payload: { id_job_title }
  }),
  activateJobTitle: id_job_title => ({
    type: "ACTIVATE_JOB_TITLE_REQUEST",
    payload: { id_job_title }
  }),
  clearSuccessJobTitle: () => ({
    type: "CLEAR_SUCCESS"
  }),
  clearError: () => ({
    type: "CLEAR_ERROR"
  }),
  clearErrorField: () => ({
    type: "CLEAR_ERROR_FIELD"
  })
};

export default actions;
