import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";
// import { Redirect } from "react-router-dom";

import JobTitleUpdateForm from "./updateForm";

import notification from "../../../../components/notification";
import { Spin } from "antd";

import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/employee/jobTitle/actions";
import { Box } from "../../../App/app.style";

const redirectTo = "/dashboard/setup/job-titles";

class UpdateJobTitle extends Component {
  // Get All data diawal
  componentDidMount() {
    const { editJobTitle } = this.props;
    const { id } = this.props.match.params;
    editJobTitle(id);
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.jobTitle.error) {
      notification("error", this.props.jobTitle.error);
      this.props.clearError();
    }

    if (this.props.jobTitle.success) {
      notification("success", this.props.jobTitle.success);
      this.props.clearSuccessJobTitle();
      this.props.history.replace(redirectTo);
    }
  }

  handleSubmit = value => {
    const { updateJobTitle } = this.props;
    const { id } = this.props.match.params;
    updateJobTitle(id, value);
  };

  render() {
    // console.log(this.props.jobTitle);
    return (
      <LayoutWrapper>
        <Box>
          <PageHeader
            title={<IntlMessages id="jobTitles.update.title" />}
            style={{ margin: 0 }}
          >
            <Button onClick={() => this.props.history.replace(redirectTo)}>
              <IntlMessages id="button.back" />
            </Button>
          </PageHeader>

          {this.props.jobTitle.loading && !this.props.jobTitle.loadingSubmit ? (
            <div className="spinStyle">
              <Spin size="large" />
            </div>
          ) : (
            <JobTitleUpdateForm
              auth={this.props.auth}
              submit={this.handleSubmit}
              jobTitle={this.props.jobTitle}
            />
          )}
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    auth: state.auth,
    jobTitle: state.jobTitle
  };
};

export default compose(connect(mapStateToProps, actions))(UpdateJobTitle);
